import styled from 'styled-components'
import { Link } from 'react-router-dom'

function HomeShowS(props) {
	return (
		<>
			<main className={props.className}>
				<div className='custom-shape-divider-top-1641961695'>
					<svg
						data-name='Layer 1'
						xmlns='http://www.w3.org/2000/svg'
						viewBox='0 0 1200 120'
						preserveAspectRatio='none'>
						<path
							d='M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z'
							opacity='.25'
							className='shape-fill'></path>
						<path
							d='M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z'
							opacity='.5'
							className='shape-fill'></path>
						<path
							d='M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z'
							className='shape-fill'></path>
					</svg>
				</div>
				<section className='main-home-panel-left'>
					<div className='separator-panel'>
						<div className='main-home-panel-left-text'>
							<h3>Make your dream computer come true.</h3>
							<h3>Be a real gamer.</h3>
						</div>
						<Link
							to='/shop/all'
							className='main-home-panel-left-btn'
							onClick={() => {
								props.cerrar()
							}}>
							SHOP NOW
						</Link>
					</div>
				</section>
				<section className='main-home-panel-rigth-img'>
					<img
						className='img-product'
						src='https://i.imgur.com/msGgRDM.png'
						alt=''
					/>
				</section>
			</main>
		</>
	)
}

const HomeShow = styled(HomeShowS)`
	min-height: calc(100vh - 3.363rem);
	display: flex;
	align-items: center;
	justify-content: center;
	align-items: center;
	background-color: ${({ theme }) => theme.mainIndex.bgColor};
	-webkit-transition: background-image 0.2s ease-in-out;
	transition: background-color 0.5s ease-in;
	overflow-y: hidden;

	.custom-shape-divider-top-1641961695 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		overflow: hidden;
		line-height: 0;
	}

	.custom-shape-divider-top-1641961695 svg {
		position: relative;
		display: block;
		width: calc(240% + 1.3px);
		height: 429px;
	}

	.custom-shape-divider-top-1641961695 .shape-fill {
		fill: ${({ theme }) => theme.mainIndex.bgColorSvg};
		transition: fill 0.5s ease-in;
	}

	.separator-panel {
		display: flex;
		flex-direction: column;
		gap: 0.5rem;
	}

	h3 {
		font-weight: 100;
		color: #fff;
		font-size: 2.2rem;
	}

	.main-home-panel-left-btn {
		border-radius: 1.875rem;
		border: none;
		width: 9rem;
		height: 3rem;
		font-size: 0.938rem;
		font-weight: 800;
		background-image: ${({ theme }) => theme.mainIndex.button};
		color: ${({ theme }) => theme.mainIndex.buttonText};
		transition: 0.25s;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: background-image 0.5s ease-in, color 0.5s ease-in,
			box-shadow 0.5s ease-in;
	}

	.main-home-panel-left-btn:hover {
		box-shadow: ${({ theme }) => theme.mainIndex.buttonShadow};
	}

	section {
		min-height: calc(100vh - 5.75rem);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 37.172rem;
		z-index: 1;
	}

	.img-product {
		width: 34.375rem;
		transition: 1s;
		position: fixed;
	}

	.img-product:hover {
		width: 35.938rem;
	}

	@media (max-width: 1025px) {
		min-height: calc(100vh- 3.3125rem);
		width: 100%;

		.main-home-panel-left {
			z-index: 1;
		}
		h3 {
			background-color: #c4c4c486;
			padding: 0.5rem;
		}
		.main-home-panel-rigth-img {
			position: absolute;
		}
		.main-home-panel {
			width: 100%;
		}
		.img-product {
			position: absolute;
			width: 25rem;
		}
		section {
			width: 100%;
			z-index: 0;
		}
	}

	@media (max-width: 1270px) {
		h3 {
			font-size: 1.5rem;
		}
	}

	@media (max-width: 750px) {
		min-height: calc(100vh - 3.063rem);
		.main-home-panel-left {
			z-index: 1;
		}
		.main-home-panel-left-text {
			padding: 0.5rem;
		}
		.main-home-panel-rigth-img {
			position: absolute;
		}
		.img-product {
			width: 18.75rem;
		}
		h3 {
			font-size: 1.25rem;
		}
	}
`

export default HomeShow
